.banner{

    background-color: #6A65DE;

    &.leaderboard-custom{
        width: 728px;
        height: 120px;

        .banner-title{
            font-size: 24px;
        }
        .banner-form{
            button{
                margin-top: 2px;
            }
        }
    }

    &.inline-rectangle{
        width: 300px;
        height: 250px;

        .banner-title{
            font-size: 22px;
        }

        .banner-form{
            p{
                margin-top: -10px;
                font-size: 14px;
                span{
                    font-size: 14px;
                    font-weight: 500;
                }
            }
        }

    }

    .banner-title{
        font-weight: 400;
        line-height: 22px;
    }

    .banner-form{
        label{
            font-size: 14px;
            margin-bottom: -2px;
        }

        span{
            font-size: 11px;
            margin-bottom: -4px;
        }
        p{
            span{
                font-size: 16px;
                font-weight: 500;
            }
        }
    }

    .btn {
        padding: 0.7rem 1.6rem;
    }

    .price{
        font-size: 34px;
    }

}